<template>
    <qw-preloader in-component
        :loading="loading">
        <div class="qw-single-t">
            <div v-if="title"
                class="qw-single-t__title">
                {{title}}
            </div>
            <div v-if="startDate && endDate && average"
                class="qw-single-t__date">
                <div class="qw-single-t__start-date">From: {{startDate | formatDateToPrint}}</div>
                <div class="qw-single-t__end-date">To: {{endDate | formatDateToPrint}}</div>
            </div>
            <div class="qw-single-t__block">
                <div class="qw-single-t__text">
                    Inside:
                </div>
                <div class="qw-single-t__icon">
                    <qw-icon-t :temperature="data.insideTemp"></qw-icon-t>
                </div>
            </div>
            <div class="qw-single-t__block">
                <div class="qw-single-t__text">
                    Outside:
                </div>
                <div class="qw-single-t__icon">
                    <qw-icon-t :temperature="data.outsideTemp"></qw-icon-t>
                </div>
            </div>
        </div>
    </qw-preloader>

</template>

<script>
import basicComponentsLogic from "@/mixins/basicComponentsLogic";
import { formatDateToPrint } from "@/helpers/utils";
export default {
    name: "QwSingleT",
    props: {
        title: String,
        average: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        afterGetData() {
            if (Array.isArray(this.data)) {
                this.data = this.data[0];
            }
        }
    },
    mixins: [basicComponentsLogic],
    filters: {
        formatDateToPrint
    }
};
</script>