<template>
    <path fill="none"
        :d="d"
        :stroke="`url(#${id})`"></path>
</template>

<script>
import { genPoints, genPath } from "./path";

export default {
    name: "QwPath",
    props: [
        "smooth",
        "graphData",
        "autoDraw",
        "autoDrawDuration",
        "autoDrawEasing",
        "boundary",
        "radius",
        "id",
        "max",
        "min"
    ],
    watch: {
        data: {
            immediate: true,
            handler() {
                this.$nextTick(() => {
                    if (!this.autoDraw) {
                        return;
                    }

                    const path = this.$el;
                    const length = path.getTotalLength();

                    path.style.transition = "none";
                    path.style.strokeWidth = "2px";
                    path.style.strokeDasharray = length + " " + length;
                    path.style.strokeDashoffset = Math.abs(
                        length - (this.lastLength || 0)
                    );
                    path.getBoundingClientRect();
                    path.style.transition = `stroke-dashoffset ${this.autoDrawDuration}ms ${this.autoDrawEasing}`;
                    path.style.strokeDashoffset = 0;
                    this.lastLength = length;
                });
            }
        }
    },
    computed: {
        d() {
            const { graphData, smooth, boundary, radius, max, min } = this;
            const points = genPoints(graphData, boundary, { max, min });
            return genPath(points, smooth ? radius : 0);
        }
    }
};
</script>