<template>
    <div class="qw-weather">
        <div class="qw-weather__buttons-block">
            <template v-for="(interval, index) in intervals">
                <button :class="interval == currentInterval ? 'is-active' : ''"
                    :key="index"
                    @click="setStartDate(interval)">
                    {{interval}}
                </button>
            </template>
        </div>
        <div class="qw-weather__current-block">
            <qw-single-t title="Current"
                api-url="https://qserver.ddns.net/qWeather/api/graph/last"></qw-single-t>
        </div>
        <div class="qw-weather__average-block">
            <qw-single-t title="Average"
                api-url="https://qserver.ddns.net/qWeather/api/graph/average"
                average
                :start-date="startDate"></qw-single-t>
        </div>
        <div class="qw-weather__graph-block">
            <qw-graph-t api-url="https://qserver.ddns.net/qWeather/api/graph"
                auto-draw
                :start-date="startDate"></qw-graph-t>
        </div>
    </div>
</template>

<script>
import moment from "moment";
export default {
    name: "Home",
    data: function() {
        return {
            startDate: "",
            currentInterval: "Day",
            intervals: ["Day", "Week", "Month", "Year"]
        };
    },
    created() {
        this.startDate = this.getTimeAgo("day");
    },
    methods: {
        setStartDate(interval) {
            this.currentInterval = interval;
            this.startDate = this.getTimeAgo(interval);
        },
        getTimeAgo(time) {
            return moment()
                .subtract(1, time)
                .format("YYYY-MM-DD");
        }
    }
};
</script>
