<template>
    <qw-preloader in-component
        :loading="loading">
        <div class="qw-graph-t">
            <div class="qw-graph-t__legend qw-legend">

                <template v-for="(item, index) in graphs">
                    <div class="qw-legend__item qw-item"
                        :key="index">
                        <div class="qw-item__color"
                            :style="`background-color:${item.gradient}`"></div>
                        <div class="qw-item__name">{{item.name}}</div>
                    </div>
                </template>

            </div>
            <svg :height="height"
                :viewBox="`0 0 ${width} ${height}`"
                :width="width">
                <defs>
                    <filter id="dropshadow">
                        <feGaussianBlur in="SourceAlpha"
                            stdDeviation="0.1"></feGaussianBlur>
                        <feOffset dx="0"
                            dy="1"></feOffset>
                        <feComponentTransfer>
                            <feFuncA type="linear"
                                slope="0.2"></feFuncA>
                        </feComponentTransfer>
                        <feMerge>
                            <feMergeNode></feMergeNode>
                            <feMergeNode in="SourceGraphic"></feMergeNode>
                        </feMerge>
                    </filter>

                    <template v-if="graphs">
                        <template v-for="(item, index) in graphs">
                            <qw-gradient :gradient="item.gradient"
                                :id="id + index"
                                :key="index"></qw-gradient>
                        </template>
                    </template>

                </defs>
                <template v-if="graphs">
                    <template v-for="(item, index) in graphs">
                        <qw-path ref="path"
                            filter="url(#dropshadow)"
                            v-bind="$props"
                            :boundary="boundary"
                            :graphData="item.data"
                            :id="id + index"
                            :key="index"></qw-path>
                    </template>
                </template>
            </svg>
        </div>
    </qw-preloader>
</template>

<script>
import basicComponentsLogic from "@/mixins/basicComponentsLogic";
export default {
    name: "QwGraphT",
    data: function() {
        return {
            data: this.graphData,
            graphs: {
                insideTemp: {
                    name: "InsideTemp",
                    gradient: ["#ffd500"],
                    data: []
                },
                outsideTemp: {
                    name: "OutsideTemp",
                    gradient: ["#45a0ff"],
                    data: []
                }
            }
        };
    },
    props: {
        graphData: {
            type: Array
        },
        autoDraw: Boolean,
        autoDrawDuration: {
            type: Number,
            default: 1000
        },
        autoDrawEasing: {
            type: String,
            default: "ease-in"
        },
        gradient: {
            type: Array,
            default: () => [["#45a0ff"], ["#ffd500"]]
        },
        max: {
            type: Number,
            default: -Infinity
        },
        min: {
            type: Number,
            default: Infinity
        },
        height: {
            type: Number,
            default: 320
        },
        width: {
            type: Number,
            default: 1024
        },
        radius: {
            type: Number,
            default: 8
        },
        smooth: Boolean
    },
    methods: {
        afterGetData() {
            this.graphs.insideTemp.data = [];
            this.graphs.outsideTemp.data = [];

            for (var value of this.data) {
                this.graphs.insideTemp.data.push(value.insideTemp);
                this.graphs.outsideTemp.data.push(value.outsideTemp);
            }
        }
    },
    computed: {
        id() {
            return "qw-graph-t-" + this._uid;
        },
        boundary() {
            return {
                minX: 0,
                minY: 0,
                maxX: this.width,
                maxY: this.height
            };
        }
    },
    mixins: [basicComponentsLogic]
};
</script>