import QwPreloader from './components/QwPreloader.vue';
import QwSingleT from './components/QwSingleT.vue';
import QwIconT from './components/QwIconT.vue';
import QwGraphT from './components/QwGraphT/QwGraphT.vue';
import QwPath from './components/QwGraphT/QwPath.vue';
import QwGradient from './components/QwGraphT/QwGradient.vue';

const qweather = {
    QwPreloader,
    QwSingleT,
    QwIconT,
    QwGraphT,
    QwPath,
    QwGradient,
};

function install(Vue) {
    Object.keys(qweather).forEach(key => {
        const Component = qweather[key];
        Vue.component(Component.name, Component);
    });
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    qweather,
};
