<template>
    <linearGradient x1="0"
        x2="0"
        y1="1"
        y2="0"
        :id="id">
        <template v-for="(color,index) in gradient">
            <stop :key="index"
                :offset="index / (gradient.length - 1 || 1)"
                :stop-color="color"></stop>
        </template>
    </linearGradient>
</template>

<script>
export default {
    name: "QwGradient",
    props: ["gradient", "id"]
};
</script>