import moment from 'moment';

export default {
    props: {
        apiUrl: {
            type: String,
            default: '',
            required: false,
        },
        startDate: {
            type: String,
            default: moment()
                .subtract(1, 'months')
                .format('YYYY-MM-DD'),
        },
        endDate: {
            type: String,
            default: moment().format('YYYY-MM-DD'),
        },
    },
    data: function() {
        return {
            data: [],
            loading: true,
        };
    },
    created() {
        this.getData();
    },
    watch: {
        startDate() {
            this.getData();
        },
        endDate() {
            this.getData();
        },
    },
    methods: {
        setRequest() {
            let app = this;
            if (Date.parse(app.startDate) && Date.parse(app.endDate)) {
                let params = new URLSearchParams();
                params.append('start', app.startDate + 'T00:00:00');
                params.append('end', app.endDate + 'T00:00:00');
                return {
                    params: params,
                };
            }
        },
        beforeGetData() {
            return true;
        },
        afterGetData() {
            this.$emit('afterGetData', this.data);
        },
        getData() {
            return new Promise(resolve => {
                if (this.apiUrl) {
                    if (this.beforeGetData) {
                        let app = this;
                        app.loading = true;
                        let request = app.setRequest();
                        app.axios
                            .get(app.apiUrl, request ? request : '')
                            .catch(function(error) {
                                throw new Error(error);
                            })
                            .then(function(response) {
                                app.$set(app, 'data', response.data);
                                app.afterGetData();
                                resolve(app.data);
                                app.loading = false;
                            });
                    }
                }
            });
        },
    },
};
