<template>
    <div class="qw-icon-t"
        :class="classes">
        <i class="qw-icon-t__icon"
            :class="iconClasses"></i>
        <span class="qw-icon-t__value">
            {{getFormattedT}}
        </span>
    </div>
</template>

<script>
export default {
    name: "QwIconT",
    props: {
        temperature: {
            type: Number,
            default: 0
        }
    },
    computed: {
        getFormattedT() {
            let t = Math.floor(this.temperature);
            return t > 0 ? (t = "+" + t) : t;
        },
        classes() {
            return [
                this.temperature > 0
                    ? "qw-icon-t--hot"
                    : this.temperature < 0
                    ? "qw-icon-t--fridge"
                    : ""
            ];
        },
        iconClasses() {
            return [
                this.temperature > 0
                    ? "fal fa-temperature-hot"
                    : this.temperature < 0
                    ? "fal fa-temperature-frigid"
                    : "fal fa-temperature-low"
            ];
        }
    }
};
</script>